<template>
  <div>
      WebBleGW
      <button v-on:click="connectBle">{{buttonTitle}}</button>
      <pre>{{msg}}</pre>
  </div>
</template>

<script>
export default {
  name: "WebBleGW",
  data: function() {
    return {
      msg: "",
      buttonTitle: "Connect"
    };
  },
  methods: {
    log: function(m) {
      this.msg += "\n" + m;
      this.$mqtt.publish("MI/log", m);
    },
    decoder: function() {
      if (this._decoder) return this._decoder;
      this._decoder = new TextDecoder("utf-8");
      return this._decoder;
    },
    connectBle: async function(event) {
      // eslint-disable-next-line no-console
      //console.log(event);
      this.msg = "";

      if (this.buttonTitle === "Disconnect") {
        if (this.device && this.device.gatt.connected) {
          await this.device.gatt.disconnect();
          //this.log("Bluetooth Device DISCONNECTED");
        } else {
          this.log("Bluetooth Device is already disconnected");
        }
        this.device = null;
        this.buttonTitle = "Connect";
        return;
      }
      this.device = await navigator.bluetooth.requestDevice({
        acceptAllDevices: true,
        optionalServices: ["c1b25000-caaf-6d0e-4c33-7dae30052840"]
      });
      this.log("Got device " + this.device.name);
      //this.log(device);

      this.device.addEventListener(
        "gattserverdisconnected",
        function(e) {
          this.log("Device disconnected.");
          this.buttonTitle = "Disconnect";
        }.bind(this)
      );

      this.log("Connecting to GATT Server...");
      const server = await this.device.gatt.connect();

      this.buttonTitle = "Disconnect";

      this.log("Getting GATT Service...");
      const service = await server.getPrimaryService(
        "c1b25000-caaf-6d0e-4c33-7dae30052840"
      );
      this.log("Got service");

      const indic_characteristics = await service.getCharacteristic(
        "c1b25010-caaf-6d0e-4c33-7dae30052840"
      );

      await indic_characteristics.startNotifications();
      this.log("Indication characteristics notifications started");
      indic_characteristics.addEventListener(
        "characteristicvaluechanged",
        function(event) {
          let value = event.target.value;
          var decoder = this.decoder();
          var decodedString = decoder.decode(value);
          this.log("val indicated: " + decodedString);
          this.$mqtt.publish(
            "MI/val",
            JSON.stringify({
              t: new Date(),
              value: decodedString
            })
          );
        }.bind(this)
      );
    }
  }
};
</script>